import {
	get,
	post,
	config,
	idcardValidate
} from '@/utils/api.js';

import cityDatas from '../../components/city.area.js';
import uniPopup from '../../components/uni-pop.vue';

export default {
	components: {
		uniPopup
	},
	
	data() {
		name: 'prize'
		return {
			bizcode: '',
			result: '',
			reply: '',
			imgUrl: config.staticUrl,		
			telToast:false, //电话号码弹框
			
			showForm: false, // 填写表单
			showtjOK: false, // 提交成功
			// 省市区地址
			provinceData: cityDatas,
			cityData: [],
			areaData: [],
			selectList: [{
				txt: '请选择'
			}, {
				txt: '请选择'
			}, {
				txt: '请选择'
			}],
			region:'', //所在地区
			tabOne: '请选择',
			indexTab: 0,
			proviceShow: true,
			areaShow: false,
			cityShow: false,
			show: false,
			type: '',
			checkOne: null,
			checkTwo: null,
			checkThree: null,
			//地址弹框 end
			yzmRes:'',// 验证码接口数据
			sec:0,
			yzval:'',
			
			regMobile : /^1[345678]\d{9}$/, //验证手机号 原来
			// regMobile : /^1\d{10}$/, //验证手机号
			regYzcode:/^[0-9]{4}$/,  //验证 验证码格式
			getted:false, //已领取过
			
			
			getCard:false,
			animationData:{} ,//动画
			off: false,
			cardImg: '' //获得的卡片
		}
	},
	onLoad(options) {
		console.log(options);
		this.bizcode = options.bizcode;
		console.log(this.result);
		console.log(this.reply);
		console.log('openid' + this.$store.state.openid) 
		
		this.result = this.$store.state.sweepData.result;
		this.reply = this.$store.state.sweepData.reply;
		
		console.log(this.result);
		console.log(this.reply);
		// prizeType 
		
		// 获得集卡
		if(this.bizcode == 11){
			this.getCard = false; //得到卡Toast不展示出来
		} else {
			if (this.reply.cardNo) {
				this.getCard = true; //得到卡Toast
				this.off = true;  //开启动画
				if (this.off) {
					// 使用动画
					setTimeout(() => {
						this.rotateAndScale()
					},200)
				} else {
					this.norotateAndScale()
				}
				this.off = !this.off;
				if (this.reply.cardNo == 'A') {
					this.cardImg = this.imgUrl + 'hongGet.png'
				} else if (this.reply.cardNo == 'B') {
					this.cardImg = this.imgUrl + 'fuGet.png'
				} else if (this.reply.cardNo == 'C') {
					this.cardImg = this.imgUrl + 'luGet.png'
				} else if (this.reply.cardNo == 'D') {
					this.cardImg = this.imgUrl + 'shouGet.png'
				} else if (this.reply.cardNo == 'E') {
					this.cardImg = this.imgUrl + 'xiGet.png'
				}
			}
		}
		
		if(options.isWrite=="true"){
		  this.getted = true
		}
	},
	onUnload(){
		
	},
	onShow(){
		var animation = uni.createAnimation({
			duration: 1000,
			timingFunction: 'ease',
		})
		this.animation = animation
		console.log(this.animation);
	},
	/**
	* 用户点击右上角分享
	*/
	onShareAppMessage(res) {
		if (res.from === 'button') {// 来自页面内分享按钮
		  console.log(res.target)
		}
		return {
		  title: '青岛啤酒',
		  path: '/pages/home/home',
		  imageUrl: this.imgUrl + 'share.png'
		}
	 },
	
	methods: {
		// 得卡的弹窗关闭
		confirm() {
			this.getCard = false;
		},
		// 定义动画内容
		rotateAndScale() {
			// 定义动画内容
			this.animation.translateY(60).rotate(360).scale(3.2).step()
			// 导出动画数据传递给data层
			this.animationData = this.animation.export()
		},
		
		toHome(){
			uni.switchTab({
				url: '../home/home',
			})
		},
		// 拨打电话
		callPhone(phoneNumber) {
			console.log(phoneNumber);
		    uni.makePhoneCall({
		       phoneNumber: String(phoneNumber),
		       success: function() {
	            console.log("拨打电话成功！")
		       },
		       fail: function() {
		        console.log("拨打电话失败！")
		       }
		    })
		},
		toRule() {
			if (this.reply.skuYear == '2018') {
				uni.navigateTo({
					url: '../rule/rule?skuKey=' + '2018',
				})
			} else if (this.reply.skuYear == '2019') {
				uni.navigateTo({
					url: '../rule/rule?skuKey=' + '2019',
				})
			} else if (this.reply.skuYear == '2020') {
				uni.navigateTo({
					url: '../rule/rule?skuKey=' + '2020',
				})
			}
		},
		showTel(){
			this.telToast = true;
		},
		closeTel(){
			this.telToast = false;
		},
		getPrize(){
			this.showForm = true;
		},
		// 校验表单是否为空
		formValue(){
			console.log(this.userName);
			// this.userName === '' || this.userName.indexOf(' ') !== -1
			if (this.userName == '' || this.userName == undefined) {
			  uni.showModal({
				title: '提示',
				content: '请填写正确的姓名哦！~',
			  })
			  return false     
			} else if (!idcardValidate(this.idcard)) {
			  uni.showModal({
				title: '提示',
				content: '请填写正确的身份证号哦！~',
			  })
			  return
			}else if (!(this.regMobile.test(this.mobile))) {
			  uni.showModal({
				title: '提示',
				content: '请填写正确的手机号！~',
			  })
			  return  // this.address === '' || this.address.indexOf(' ') !== -1
			}else if (this.address == '' || this.address == undefined) {
			  uni.showModal({
				title: '提示',
				content: '请填写正确的详细地址哦！~',
			  })
			   return
			}else if (!(this.regYzcode.test(this.yzval))) {
			  uni.showModal({
				title: '温馨提示',
				content: '请填写正确的验证码哦！~',
			  })
			  return false
			} else {
				for(var x in this.selectList){
				   if(this.selectList[x].txt == '请选择') {//x为数组的下标
				      uni.showModal({
						title: '提示',
						content: '你还没有选择完整的所在地区信息哦~',
				      })
					  return false
				   } 
				}
			}
			this.sub_message();
		},
		
		async sub_message() {
			if (!this.reply || this.reply.prizeVcode == '' || this.reply.prizeVcode == 'undefined') {
				uni.showModal({
				  title: '提示',
				  content: '信息缺失，无法提交信息！',
				})
				return 
			}
			uni.showLoading({
				title: '提交中...',
			})
			var params = {
				"openid": this.$store.state.openid, // 
				"username": this.userName,
				"address":this.region + this.address.trim(),
				"idcard": this.idcard, // 'idcard'
				"phonenum":this.mobile,
				"prizeVcode":this.reply.prizeVcode,
				// "prizeInfoKey":this.reply.grandPrizeType,
				"captcha":this.yzval,
				"projectServerName": 'liaoning',
			};
			this.tjResult = await post('/user/savePrize', params);
			console.log(this.tjResult); // reply result replyTime
			uni.hideLoading();
			var jo = this.tjResult;
			
			if (jo.result.businessCode == 0) {
				 this.showtjOK = true;
				  setTimeout(() => {
					this.showForm = false;
					this.getted = true;  //已兑奖戳 出现  领取按钮消失
				  }, 2000)
				  this.clearForm();
			} else {
			  uni.showModal({
				title: '温馨提示',
				content: jo.result.msg,
			  })
			}
		 },
		 
		 clearForm(){  //  清空表单
		 	this.userName = '';
			this.idcard = '';
		 	this.mobile = '';
		 	this.region = '';
		 	this.address = '';
		 	this.yzval = '';
		 	this.seeInfo = false;
		 },
		// 成功后我知道了
		iknow() {
			this.showForm = false;
			this.showtjOK = false;
		},
		// 获取验证码
		async getCaptcha() {
			uni.showLoading({
				title: '获取中...',
			})
			var params = {
				"phonenum": this.mobile,
				"projectServerName": 'liaoning',
			};
			this.yzmRes = await post('/user/getCaptcha', params);
			console.log('getCaptcha' + this.yzmRes); // reply result replyTime
			uni.hideLoading();
			var jo = this.yzmRes;
			if (jo.result.businessCode == 0) {
			  this.sec = 60;
			  this.timer();
			} else {
			  uni.showModal({
			    title: '提示',
			    content: jo.result.msg,
			  })
			}
		 },
		
		getyz() {
		    if (this.sec > 0) {
		      return false;
		    }
		    if (!(/^1[34578]\d{9}$/.test(this.mobile))) {
		      uni.showModal({
		        title: '提示',
		        content: '请填写正确的手机号！~'
		      })
		    } else {
				this.getCaptcha();
				this.sec = 0;
		    }
		},
		timer() {
			let timer = null;
			if (this.stop || this.sec <= 0) {
			  this.sec = 0;
			  return false;
			}
			this.sec = this.sec - 1;
			timer = setTimeout(() => {
			  this.timer()
			}, 1000);
		},
		
		// 省市区地址
		togglePopup(type, open) {
			if(this.isDisabled == true){
				return  //不可以点击
			} else {
				this.type = type
				if (open === 'tip') {
					this.show = true
				} else {
					this.$refs[open].open()
				}
			}
		},
		cancel(type) {  //取消地选择框
			if (type === 'tip') {
				// 选择地区框消失
				this.show = false
				return		
			}
			this.$refs[type].close();
			console.log(this.selectList);
			// 校验是否没有进行选择
			for(var x in this.selectList){
				console.log('aaa')
			   if(this.selectList[x].txt == '请选择') {//x为数组的下标
			      console.log('bbb');
			      uni.showModal({
					title: '提示',
					content: '你还没有选择完整的所在地区信息哦~',
			      })
				  return
			   } else{
				   this.region = this.selectList[0].txt+this.selectList[1].txt + this.selectList[2].txt + '-';
				   console.log(this.region);
				   // console.log(this.selectList[0].txt)
				   // 选择地区框消失
				   this.show = false
				   return
			   }
			}
		},
		change(e) {
			if (e.show == true) {
				uni.hideTabBar()
			} else {
				uni.showTabBar()
			}
		},
		tabEvent(index) {
			this.indexTab = index
			if (this.indexTab == 0) {
				this.proviceShow = true
				this.cityShow = false
				this.areaShow = false
				// this.checkOne = null
				this.checkTwo = null
				this.checkThree = null
				// this.cityData = []
				this.areaData = []
				// this.selectList[0].txt = "请选择"
				this.selectList[1].txt = "请选择"
				this.selectList[2].txt = "请选择"
			} else if (this.indexTab == 1) {
				this.proviceShow = false
				this.cityShow = true
				this.areaShow = false
				// this.checkTwo = null
				this.checkThree = null
				// this.areaData = []
				// this.selectList[1].txt = "请选择"
				this.selectList[2].txt = "请选择"
			} else if (this.indexTab == 2) {
				this.proviceShow = false
				this.cityShow = false
				this.areaShow = true
			}
		},
		provinceEvent(data, index) {
			this.checkOne = index
			this.selectList[0].txt = data.name
			this.indexTab = 1
			this.proviceShow = false
			this.cityShow = true
			this.areaShow = false
			this.cityData = data.cityList
		},
		cityEvent(data, index) {
			this.checkTwo = index
			this.selectList[1].txt = data.name
			this.indexTab = 2
			this.proviceShow = false
			this.cityShow = false
			this.areaShow = true
			this.areaData = data.areaList
		},
		areaEvent(data, index) {
			this.checkThree = index
			this.selectList[2].txt = data.name
		},
	}
}
